<script>
    import { swiper, swiperSlide } from 'vue-awesome-swiper'
    import lottie from 'lottie-web'
    import radar from '@/api/radar.json' // 引入json文件
    import api from '@/api/equipSignaStreng.json'
    export default {
        name: 'mainDevice',
        components:{
            swiper,swiperSlide
        },
        data(){
            return {
                swiperOption: {                     //swiper无缝滚动配置项
                    direction: "vertical",
                    autoplay: true,
                    slidesPerView:6
                },
                devSignStrenthData:[],
            }
        },
        methods:{
            devSignStrenthList(){
                this.devSignStrenthData = api.equipSignStreng;
            },
        },
        mounted() {
            var radarBO =  lottie.loadAnimation({
                animationData:radar,  loop:true,
                autoplay:true,
                renderer:'svg',
                container: this.$refs.radar, // 容器 //渲染方式，有"html"、"canvas"和"svg"三种
            });
            radarBO.play()
            this.devSignStrenthList();
        }
    }
</script>

